/*------------------------------------*\
    #TYPOGRAPHY
  \*------------------------------------*/
:root {
  --headline-1: 3rem;
  --headline-2: 2.5rem;
  --headline-3: 2.375rem;
  --headline-4: 2rem;
  --subtitle: 1.5rem;
  --paragraph-1: 1.375rem;
  --paragraph-2: 1.25rem;
  --body-1: 1rem;
  --body-2: 0.875rem;
  --body-3: 0.75rem;
}

@media (max-width: 768px) {
  :root {
    --headline-1: 2rem;
    --headline-2: 1.5rem;
    --headline-3: 1.375rem;
    --headline-4: 2rem;
    --subtitle: 1.25rem;
    --paragraph-1: 1.125rem;
    --paragraph-2: 1.125rem;
    --body-1: 1rem;
    --body-2: 0.875rem;
    --body-3: 0.75rem;
  }
}
